.App {
  text-align: center;
  background-image: url("./imagenes/fondo1.jpg");
  background-size: cover;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: center;
  justify-items: center;
  
}

.contenedor{
  background-color: #11998ed7;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  justify-items: center;
  flex-direction: column;
  width:40%;
  height: 90%;
  border-radius: 5%;


}

@media only screen and  (max-width: 600px){

  .contenedor{
    width:100%;
    height: 100%;
  }
     
  }




.contenedorlogo{
  display: flex;
  justify-content: center;
  justify-items: center;
  align-items: center;
  height:30% ;
  width: 40%;
 
}

@media only screen and  (max-width: 600px){

  .contenedorlogo{
 
    height:20% ;
    width: 40%;
  margin-top: 0;
  }
}

.logo{
  border-radius: 50%;
  height: 10rem;
  width: 10rem;
  
}

@media only screen and  (max-width: 600px){

  .logo{
 
    height: 5rem;
    width: 5rem;
    
  }
  }




.informacion{
  font-family: 'Rubik', sans-serif;
  font-weight: bold;
  width: 60%;
  height: 15%;
  display: flex;
  justify-content: center;
  justify-items: center;
  flex-direction: column;
 
}

@media only screen and  (max-width: 600px){

  .informacion{
  
    width: 90%;
  }
  }



.contenedorInput{

  display: flex;
  justify-content: center;
  justify-items: center;
  align-items: center;
  align-content: center;
  flex-direction: column;
  width: 80%;
}

@media only screen and  (max-width: 600px){

  .contenedorInput{
    width: 100%;
  }
  }



.form-control{
  font-size: 2rem;
  height: 75%;
  width: 80%;
  display: flex;
  justify-content: center;
  justify-items: center;
  flex-direction: column;
  text-align: center;
 
}

.tipodecambio{
  display:flex ;
  flex-direction: row;
  justify-content: center;
  justify-items: center;
  width: 80%;


}

@media only screen and  (max-width: 600px){

  .tipodecambio{
 
    width: 100%;
  }
  }


.contenedor1{
  margin:1rem;
